/*home style*/
.page-home{background: url(./img/bg1.jpg); background-size:cover; padding-top:38px; min-height: 100vh;}

#main-menu{ position: absolute; width: 15%; left: 15%; padding-right: 10px;}

.xui-card{ position: absolute; width: 40%; left: 30%; z-index: 10; opacity: 0; }
.xui-card[active-page='true']{ z-index: 20; opacity: 1; }
#home-card{  z-index: 99; opacity: 1; }

.wide-view #main-menu{ left: 0%; }
.wide-view #home-card{ left: 15%; }
.wide-view .xui-card[active-page='true']{ left: 55%; margin-left: 10px; }

/*resume*/
.resume-buttons{margin-bottom:20px;}
.resume-buttons a{ font-size: 10px;padding:8px 16px; font-weight: normal; }
.resume-item{padding:15px;border: 4px double #ccc;margin-bottom:15px;}
.resume-item .title{font-weight: bold;font-size: 18px;color:#333;}
.resume-item .sub-title{ font-size: 11px; font-style: italic; border-bottom: 1px solid #ccc;padding-bottom:5px; line-height: 20px; }
.resume-item .description{ padding:5px;font-size: 12px; }

/*skills*/
.skill-list{margin-top:20px;}

/*cases*/
#case-card .list-view .item{ border: 1px solid #ccc; padding: 8px; text-align: center; position: relative; }
#case-card .list-view .item a{ text-decoration: none; }
#case-card .list-view .item img{ width: 100%; }
#case-card .list-view .item h2{
    position: absolute;
    top: 24px;
    left: 24px;
    line-height: 24px;
    margin-bottom: 12px;
    text-align: left;
    color: #fff;
    text-shadow: 0 1px rgba(0, 0, 0, .8);
    font-size: 16px;
    font-weight: bold;
}