.experience-item {
  margin-bottom: 24px;
}
.experience-item:last-child {
  margin-bottom: 0;
}
.experience-item .meta {
  display: flex;
  height: 24px;
  line-height: 24px;
  margin-bottom: 12px;
}
.experience-item .meta .name {
  flex: 1 0 25%;
  font-weight: bold;
}
.experience-item .meta .period {
  flex: 1 0 20%;
}
.experience-item .meta .role {
  flex: 1 0 30%;
}
.experience-item .detail {
  padding-left: 32px;
  line-height: 28px;
  margin-bottom: 12px;
}
.experience-item .detail ol {
  list-style: decimal;
  padding-left: 20px;
}
.experience-item .detail p {
  text-indent: 2em;
}
.experience-item .experience-desc h3,
.experience-item .experience-achievement h3 {
  font-weight: bold;
  line-height: 24px;
  margin-bottom: 8px;
  color: #999;
  border-left: 3px solid #ccc;
  padding-left: 8px;
}
