.resume-section {
  margin: 0 48px 12px;
}
.resume-section .section-header {
  font-size: 20px;
}
.resume-section .section-header h2 {
  display: flex;
  align-items: center;
  height: 32px;
  font-weight: bold;
}
.resume-section .section-header .icon {
  width: 24px;
  height: 24px;
  display: inline-block;
}
.resume-section .section-header .icon img {
  width: 100%;
}
.resume-section .section-header .title {
  margin-left: 12px;
}
.resume-section .section-content {
  padding: 16px 0;
}
.resume-section p {
  line-height: 2;
  text-indent: 2em;
}
