.resume-header {
  background: #A5A5A5;
  display: flex;
  align-items: center;
  margin-bottom: 24px;
  padding: 0 32px;
}
.resume-header .avatar {
  width: 200px;
  height: 200px;
}
.resume-header .avatar img {
  display: block;
  border-radius: 50%;
  width: 160px;
  height: 160px;
  margin: 20px;
}
.resume-header .name {
  flex: 1 1;
  padding: 24px;
}
.resume-header .name h1 {
  font-size: 36px;
  margin-bottom: 48px;
}
.resume-header .name h2,
.resume-header .name h3 {
  font-size: 16px;
  line-height: 36px;
}
.resume-header .meta-info {
  width: 250px;
}
.resume-header .meta-info li {
  line-height: 32px;
  height: 32px;
  margin-bottom: 8px;
}
.resume-header .meta-info li i.iconfont {
  font-size: 22px;
  margin-right: 8px;
}
